#dnd-spellbook {
  --color-text: #23130a;
  --color-parchment-light: #fffef0;
  --color-parchment-dark: #d9b28a;
  --parchment-blur: 125px;
  --shadow-md: 0 4px 16px #00000080;
  --parchment-background-image: url(data:image/png;base64,);
  --shadow-parchment: 0 0 var(--parchment-blur) var(--color-parchment-dark) inset;

  box-shadow: var(--shadow-md), var(--shadow-parchment);
  background-image: var(--parchment-background-image);
  background-color: var(--color-parchment-light);
  color: var(--color-text);

  & img {
    position: relative;
    border-top: 1px solid var(--color-parchment-dark);

    @media (min-width: 668px)  {
      border: none;
      border-left: 1px solid var(--color-parchment-dark);
    }

    &::after {
      content: '';
      display: block;
      position: absolute;
      inset: 0;
      box-shadow: inset var(--shadow-md);
    }
  }
}